	module.exports = {"title":"Gatsby","date":"2016-01-04 08:05:07 (月)","layout":"post","path":"/20160104/","body":"<p>カンムお土産の定番 <a rel=\"nofollow\" href=\"http://www.amazon.co.jp/gp/product/B007PNXIMM/ref=as_li_ss_tl?ie=UTF8&camp=247&creative=7399&creativeASIN=B007PNXIMM&linkCode=as2&tag=ideyuta-22\">大辛口 柿の種</a><img src=\"http://ir-jp.amazon-adsystem.com/e/ir?t=ideyuta-22&l=as2&o=9&a=B007PNXIMM\" width=\"1\" height=\"1\" border=\"0\" alt=\"\" style=\"border:none !important; margin:0px !important;\" /> を社長が買ってきた。\n普通の柿の種よりも辛味が強くてクセになる。消費速度が尋常じゃない。</p>\n<hr>\n<h2>Gatsby</h2>\n<p>静的サイトジェネレータのGatsbyが結構よさそう。</p>\n<p><a href=\"https://github.com/gatsbyjs/gatsby\">https://github.com/gatsbyjs/gatsby</a></p>\n<pre><code>app.js\nconfig.toml\nhtml.jsx\npages\n<span class=\"hljs-string\">|___ _template.jsx</span>\n<span class=\"hljs-string\">|___ index.jsx</span>\n\\___ <span class=\"hljs-number\">2016</span>\n     <span class=\"hljs-string\">|___ hoge.md</span>\n     \\___ fuga.md\nwrappers\n\\___ md.jsx\n</code></pre>\n<p>こんな感じの基底ファイル群を作って<code>gatsby build</code>すればMDファイルからHTMLをジェネレートしてくれる。JS/Reactで書けるので個人的には扱いやすくて🙆 ただ、隠蔽されてる処理が多いため、基底ファイル群の関係がわかりにくく若干気持ち悪い（<code>wrappers/md.jsx</code>がどこからもimportされてないけど各ページのtemplateになってるとか）。</p>\n<p>new postのようなコマンドがないので、ディレクトリとmdファイルを作成しなければならないのはちょっと手間（リサーチがあまいだけであるのかも？）。</p>\n<p>あと、React-routerのバージョンが低いのが気になるけど、どうやら対応中っぽかった。メンテされてるのも🙆\n<a href=\"https://github.com/gatsbyjs/gatsby/issues/32\">Upgrade to React 0.14 and React Router 1.0 #32</a></p>\n"}

