	module.exports = {"title":"御神酒と砂コーヒー","date":"2016-01-05 07:24:34 (火)","layout":"post","path":"/20160105/","body":"<p>伊勢の御神酒をみんなで飲む。気合い入れていくんやで</p>\n<h2>ロゴ</h2>\n<p><a href=\"http://gigazine.net/news/20160104-truly-great-logo/\">ロゴ作りで大事なのはロゴの見た目にあらず、本当に大切なポイントを解説したムービー「What makes a truly great logo」</a></p>\n<p>ヒラリー・クリントンのロゴ・システムは本当によくできてた。どう展開しブランドとしての知名度を向上させていくかまで考えられた、ロゴというシステムを作ることが重要。ただ、初めからそんなにうまくやるのは難しいし、できなくてもなんとかなるかもねーっていうナイキの話。</p>\n<iframe width=\"560\" height=\"315\" src=\"https://www.youtube.com/embed/RBTiTcHm_ac\" frameborder=\"0\" allowfullscreen></iframe>\n<h2>React Nativeでカスタムフォントを使うには</h2>\n<p><a href=\"https://medium.com/@dabit3/adding-custom-fonts-to-react-native-b266b41bff7f#.dvlp6hbup\">Medium : Adding Custom Fonts to A React Native Application for IOS</a></p>\n<p>RN v0.16.0ではこのやり方でいけた。\nでもリソースファイルがプロジェクト内にないっぽいけどどうなってるんやろ…とりあえずプロジェクトディレクトリ内にリソースファイルを直接入れてそこを参照するようにしておく。問題があればパッケージング時に何か発生すると思われる…</p>\n<h2>砂コーヒーがうまい</h2>\n<p>まさひこ（<a href=\"https://twitter.com/moqada\">@moqada</a>）さんの買ってきた<a href=\"https://shop.ponits.jp/products/detail.php?product_id=665\">砂コーヒー</a>がうまかった。酸味がなく苦目だけど飲み口柔らかな感じ。\nすなば珈琲で飲めるっぽいけど、作ってるのはすなば珈琲じゃないらしい。240度に熱した砂丘の砂で焙煎するっていうなんかすごいやつ。</p>\n<blockquote class=\"twitter-tweet\" lang=\"ja\"><p lang=\"ja\" dir=\"ltr\">砂コーヒーうまい ☕️</p>&mdash; ☺︎ (@_ideyuta) <a href=\"https://twitter.com/_ideyuta/status/684225603642380288\">2016, 1月 5</a></blockquote>\n<h2>クレカ番号の規格</h2>\n<p><a href=\"http://force4u.cocolog-nifty.com/skywalker/2010/07/ocrfarrington-7.html\">http://force4u.cocolog-nifty.com/skywalker/2010/07/ocrfarrington-7.html</a></p>\n<p>番号のフォントはFarrington 7B（<a href=\"http://www.morovia.com/fonts/farrington-7b/\">これ</a>とか<a href=\"http://www.barcodesoft.com/ja-jp/farrington7b_font.aspx\">これ</a>）やOCR-Aといったフォントが使われるそう。\n<a href=\"http://www.myfonts.com/fonts/wundes/caard/large-numbers/glyphs.html\">CAARD</a>という似たフォントもあるそうな。</p>\n<p><a href=\"http://card.jbmia.or.jp/hyoujyun/hyoujun-jisgaiyo.html#c1_6301\">カードのJIS規格いろいろ</a></p>\n<h2>南極の虹</h2>\n<p>綺麗。南極は無理だけど北極は電車でいけるんだっけ？</p>\n<blockquote class=\"twitter-tweet\" lang=\"ja\"><p lang=\"ja\" dir=\"ltr\">初めて見た。南極の虹。 <a href=\"https://t.co/pVGM9IOubb\">pic.twitter.com/pVGM9IOubb</a></p>&mdash; KAGAYA (@KAGAYA_11949) <a href=\"https://twitter.com/KAGAYA_11949/status/684017384538136577\">2016, 1月 4</a></blockquote>\n<h2>カレンダー共有アプリ「TimeTree」が語るユーザー対話のコツ。</h2>\n<p><a href=\"http://appmarketinglabo.net/timetree/\">http://appmarketinglabo.net/timetree/</a></p>\n<p>意外なところからの拡散、レビューの促し方など、最近よくあるパターンっぽい。MERYのレビュー/不具合報告の動線とかも参考になる。嬉しい気持ちはレビューへ、悲しい気持ちは不具合報告へ。</p>\n<h2>ホーリーケーン</h2>\n<blockquote class=\"twitter-tweet\" lang=\"ja\"><p lang=\"und\" dir=\"ltr\"><a href=\"https://t.co/vkEF3LTVj4\">pic.twitter.com/vkEF3LTVj4</a></p>&mdash; くろけん@P (@cro_ken_P) <a href=\"https://twitter.com/cro_ken_P/status/683994251751849984\">2016, 1月 4</a></blockquote>\n"}

