	module.exports = {"title":"POSTALCO","date":"2016-01-06 06:52:44 (水)","layout":"post","path":"/20160106/","body":"<h2>GatsbyでTwitter widgetのscriptがうまく読み込まれない問題</h2>\n<p>GatsbyはReact製なので、遷移した時にPage部分だけ再描画されてTwitterWidgetのScriptが適用されないっぽい。<code>_template.jsx</code>内の<code>componentDidUpdate</code>で常にScriptを読み込み直すようにする必要があった。</p>\n<h2>Inside Apple Part 1</h2>\n<p><a href=\"http://www.cbsnews.com/videos/inside-apple-part-one/\">http://www.cbsnews.com/videos/inside-apple-part-one/</a></p>\n<embed src=\"http://www.cbsnews.com/common/video/cbsnews_video.swf\" scale=\"noscale\" salign=\"lt\" type=\"application/x-shockwave-flash\" background=\"#000000\" width=\"620\" height=\"387\" allowFullScreen=\"true\" allowScriptAccess=\"always\" FlashVars=\"pType=embed&si=254&pid=9ABmaqLfVFU4&uuid=52b44a81-7872-4762-aab0-02f5686ab51d&url=http://www.cbsnews.com/videos/inside-apple-part-one/\" />\n<h2>Status Simple</h2>\n<p><a href=\"https://status.simple.com/\">https://status.simple.com/</a></p>\n<p>サービスを享受できない理由をユーザに伝えることの大切さ。</p>\n<blockquote>\n<p>決済系サービスは色んな事業者を繋がないといけなくて、それを束ねてユーザに提供してるから、俺たちの責任でなくてもわかりやすく提示していかんといかんよな。（by achiku）</p>\n</blockquote>\n<h2>家</h2>\n<p><a href=\"http://portalshit.net/2014/12/11/thought-on-own-house\">家を建てたので得られた知見を共有します</a></p>\n<h2>POSTALCO NoteBook</h2>\n<p>FacebookのリードデザイナーSharon Matas（Mike Matas の奥さん）が使っているスケッチブック</p>\n<p><a href=\"http://postalco.net/products/20124_regular/\">POSTALCO NoteBook</a></p>\n<blockquote class=\"twitter-tweet\" lang=\"ja\"><p lang=\"en\" dir=\"ltr\">Looking back at 2015 through my traveling sketchbook. <a href=\"https://t.co/Gsv0hn3mBe\">pic.twitter.com/Gsv0hn3mBe</a></p>&mdash; Sharon Matas (@mycookingdiary) <a href=\"https://twitter.com/mycookingdiary/status/683042661670912001\">2016, 1月 1</a></blockquote>\n<p>1mm方眼のリングノートって珍しい気がする。そこそこいい値段するけどかっこいい</p>\n"}

