	module.exports = {"title":"最後に見る夢をいくらで買いますか？","date":"2016-01-16 06:46:54 (土)","layout":"post","path":"/20160116/","body":"<h2>最後に見る夢をいくらで買いますか？</h2>\n<p><a href=\"http://d.hatena.ne.jp/grand_bishop/touch/20150402/1427945550\">http://d.hatena.ne.jp/grand_bishop/touch/20150402/1427945550</a></p>\n<blockquote>\n<p>父は一つ一つ自分が死んだあとのことを話した後、「お母さんたのんだで」と言った。抗ガン剤の副作用は酷いらしく、意識がもうろうとする中、夢を見ると言っていた。僕と弟が小さかった時の夢らしい。</p>\n</blockquote>\n<blockquote>\n<p>自分の最期に見る夢はどんな夢がいいだろう？その為に払うコストと労力は安いか高いかどっちだろう？</p>\n</blockquote>\n<blockquote>\n<p>子育ては大変なことばかりだし、手も時間もお金もかかる。でも最期に見る夢は、死ぬ直前に作ることも買うこともできない。何億持って居たとしてもだ。</p>\n</blockquote>\n<h2>元町</h2>\n<p>駅を出て港の見える丘公園へ。階段が長くきつい。<br>\n丘へ登ると、毎日散歩をしているという二人組のお爺さんと出会う。ひとりのお爺さんは観光客の写真を撮ってあげるのが趣味なようで、カメラを構えてる人を見かけては「撮ってあげるよ」と声をかけている。記念に3人で撮ってもらった。<br>\nもうひとりのお爺さんはお話好きで、木更津の煙突やスカイツリー、羽田空港や新横浜のタワービルなど、丘から見える景色を解説してくれた。3月に横浜港へクイーン・エリザベス号が来航する話から、エリザベス号はベイブリッジよりも1メートルほど高くて通れない、けれど引き潮の時だけは通れるんだといった話など、面白い話が聞けた。</p>\n<p>娘の授乳室を探しに元町商店街まで戻る。スマホで検索して<a href=\"http://www.motomachi.or.jp/shop/%E3%83%91%E3%82%A6%E3%83%80%E3%83%BC%E3%83%AB%E3%83%BC%E3%83%A0%E5%85%83%E7%94%BA%E3%82%AA%E3%82%A2%E3%82%B7%E3%82%B9\">元町オアシス</a>という施設を見つける。ポンパドールの斜め向かいのビルの2階にあり、授乳・オムツ替えなどが可能。綺麗だしお湯も出るしトイレもあるし良い施設。</p>\n<p>商店街を歩いているとカフェインレスのコーヒーを置いている店があったので入ってみる。<a href=\"http://www.caravan-coffee.jp/corporate.html\">キャラバンコーヒー</a>といい1928年創業の老舗らしいが、最近リニューアルオープンしたらしく綺麗だった。インペリアルコーヒーとカフェインレスコーヒー、ピザトーストを頼んで待つ。うまい。</p>\n<p>キャラバンコーヒーを出るとすぐ左手に中華街への道がある。江戸清によって豚まんを買って帰路につく。</p>\n<p>帰り道の練り物屋で350円の練り物セットを買う。夕飯にそばと一緒に食べる。</p>\n"}

