	module.exports = {"title":"木村、お前タイムリープしてねえ？","date":"2016-01-20 06:33:30 (水)","layout":"post","path":"/20160120/","body":"<h2>Sofarにスタイルを追加</h2>\n<p>ページリスト、ポストページのスタイルを調整した。<br>\n<a href=\"http://www.hcn.zaq.ne.jp/___/WEB/css-writing-modes-ja.html\">CSS Writing Modesが2015年12月15日付でLevel3になった</a>ので、縦書きにしてみても良いかもしれん。</p>\n<h2>Elevation | Material Design</h2>\n<p><a href=\"https://www.google.com/design/spec/what-is-material/elevation-shadows.html#elevation-shadows-elevation-android-\">Elevation and shadows</a></p>\n<p>Material DesignのElevation参考になる。</p>\n<h2>柴田文江×田川欣哉×原研哉「2016年、デザインは何ができるのか？」</h2>\n<p><a href=\"http://mag.sendenkaigi.com/brain/201602/aoyama-meeting/007220.php\">http://mag.sendenkaigi.com/brain/201602/aoyama-meeting/007220.php</a></p>\n<blockquote>\n<p>原：移動に伴う享楽が“世界共通の喜び”になり、行動のモチベーションになる。それによって、デザインやテクノロジーの問題の中枢は「移動の喜びに対して何ができるか」ということに移っていくと考えられるわけです。</p>\n</blockquote>\n<blockquote>\n<p>柴田：メーカーはその幻想を抱いて商品開発をしがちですが、私自身も所有より移動や行動欲求のほうが高くなっているから、ものづくりが所有のためではなくなっていることには大きな納得感があります</p>\n</blockquote>\n<blockquote>\n<p>田川：UBERによって、タクシーを呼ぶことから乗車中の体験、そして決済までが、シンプルに解決されました。これはまさにエクスペリエンスのデザイン。世界移動者数の増加とシンクロするように、都市の体験の仕方、場合によっては都市そのものの成り立ちもITによって変化し得ることを示していると思います。</p>\n</blockquote>\n<h2>SnowPeak 2016 Outdoor Lifestyle Catalog</h2>\n<p><a href=\"https://store.snowpeak.co.jp/sp/catalog2016/\">https://store.snowpeak.co.jp/sp/catalog2016/</a></p>\n<blockquote>\n<p>大切な人と過ごす時間。ゆっくりと料理をつくって食べる時間。太陽の動きとともに、居場所をつくり、眠り、起きる。スノーピークが提案する、人間らしさを取り戻すための野遊びの可能性を、274ページに凝縮した最新刊。</p>\n</blockquote>\n<p>これがヴィジョンや。</p>\n<h2>木村、お前タイムリープしてねえ？</h2>\n<blockquote class=\"twitter-tweet\" lang=\"ja\"><p lang=\"ja\" dir=\"ltr\">話題の「木村拓哉タイムリープ説」を実写化してみましたwww <a href=\"https://t.co/EJ4ZhRUcBY\">pic.twitter.com/EJ4ZhRUcBY</a></p>&mdash; のぶ / 北の打ち師達 (@nobu_llllllllll) <a href=\"https://twitter.com/nobu_llllllllll/status/689753453069860865\">2016, 1月 20</a></blockquote>\n"}

