	module.exports = {"title":"SUSHI食べたい","date":"2016-02-12 06:45:43 (金)","layout":"post","path":"/20160212/","body":"<h2>SUSHI食べたい</h2>\n<blockquote>\n<p>\\ﾍｲﾗｯｼｬｲ/SUSHI🍣食べたい🙏 この世🌏の終わり👋の日は SUSHI🍣食べたい🙏 最後💥💣の晩餐🍴に😇SUSHI🍣食べたい🙏 特別🎂🎉🏆💏なあの日📆には SUSHI🍣食べたい🙏🙏 トロ🐟タコ🐙ウニ✳︎いくら💰 🔊ﾃｰﾚｰﾚｰ↑(セ〜〜〜ル)(都会のオアスシ)</p>\n</blockquote>\n<iframe width=\"560\" height=\"315\" src=\"https://www.youtube.com/embed/epfPe2U_2Xk?start=88\" frameborder=\"0\" allowfullscreen></iframe>\n<h2>分析美学</h2>\n<p><a href=\"http://synodos.jp/culture/16122\">分析美学ってどういう学問なんですか――日本の若手美学者からの現状報告 森功次 / 美学者</a></p>\n<blockquote>\n<p>「分析美学って何ですか」という質問への教科書的、かつ穏当な答え方は、「分析哲学の手法を用いて美学的な問題をあつかう学問ですよ」というものだろう。</p>\n</blockquote>\n<blockquote>\n<p>美学とは、〈美しいもの〉や〈面白いもの〉などを見てとるときに働く「感性」（注3）について考える学問である。</p>\n</blockquote>\n<blockquote>\n<p>「分析哲学」という言葉を、初期の言語分析に特化した時期の動向だけを指すものとして使う人もいるが、むしろ最近は、「初期の分析哲学の伝統を引き継ぎつつ行われている現代の哲学」という広い意味で用いられることが多い。</p>\n</blockquote>\n<blockquote>\n<p>分析美学の中では長らく「芸術を定義することができるか」とりわけ「芸術に必要十分条件を与えることができるか」ということが大きな問題となっていた</p>\n</blockquote>\n<blockquote>\n<p>分析系哲学者たちは、「論争を通じての理論の前進」をとても重視する。もちろん、学問を評価するための評価軸は様々ありうるが、分析系の哲学はその中でも、「より良い論争を行うこと」にとりわけ高い優先順位を与えるのだ。</p>\n</blockquote>\n<blockquote>\n<p>分析美学は、「まずカントを読もう」「まずはプラトンを読もう」ところにスタートラインを置くのではなく、「何故これがアートなの？」「音楽作品って結局何なの？」「何故こんなに不道徳な作品が評価されてるの？」といった素朴な疑問から議論をスタートさせる。</p>\n</blockquote>\n"}

